header {
  display: flex;
  grid-area: header;
  background-color: rgb(51, 51, 51);
  padding: 0 50px;
  position: relative;
}

header h1 {
  color: white;
  margin: 0;
}

header h3 {
  color: rgb(220, 217, 217);
  margin: 0;
  position: relative;
  left: 15px;
  top: -5px;
}

.myPhoto {
  width: 250px;
  height: 250px;
  background-image: url('../../../public/images/ya.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 100%;
  position: relative;
  top: 35px;
}

.dispFlex {
  display: flex;
}

.contacts {
  background-color: rgb(220, 217, 217);
  border: 3px solid white;
  border-radius: 30px;
  padding: 10px 20px 10px 20px;
}

.contactsWrapper {
  position: relative;
  top: 100px;
  left: 10px;
}

.contactsEmail {
  margin-right: 30px;
}

.contactsIcon {
  align-self: center;
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
