.App {
  display: grid;
  grid-template-areas:
    'header header'
    'aside main'
    'footer footer';
  column-gap: 40px;
  grid-template-rows: 200px 1fr 50px;
  grid-template-columns: 0.8fr 2fr;
  position: relative;
  overflow: hidden;
}

.otherApp {
  display: grid;
  grid-template-areas:
    'header header'
    'main main'
    'footer footer';
  column-gap: 40px;
  grid-template-rows: 200px 1fr 50px;
  grid-template-columns: 0.8fr 2fr;
  position: relative;
  overflow: hidden;
}

@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

body {
  background-color: #333;
  color: #222;
  overflow-x: hidden;
}

.container {
  background-color: #fafafa;
  transform-origin: top left;
  transition: transform 0.5s linear;
  min-height: 100vh;
}

.container.show-nav {
  transform: rotate(-20deg);
}

.circle-container {
  position: absolute;
  top: -110px;
  left: -110px;
  z-index: 100;
}

.circle {
  background-color: orangered;
  height: 200px;
  width: 200px;
  border-radius: 50%;
  position: relative;
  transition: transform 0.5s linear;
}

.container.show-nav .circle {
  transform: rotate(-70deg);
}

.circle button {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  height: 100px;
  background: transparent;
  border: 0;
  font-size: 26px;
  color: #fff;
}

.circle button:focus {
  outline: none;
}

.circle button.open {
  left: 65%;
}

.circle button.close {
  top: 60%;
  transform: rotate(65deg);
  transform-origin: top left;
}

.container.show-nav + nav li {
  transform: translateX(0);
  transition-delay: 0.3s;
}

.castomNav {
  position: fixed;
  bottom: 40px;
  left: 0;
  z-index: 100;
}

.castomNav ul {
  list-style-type: none;
  padding-left: 30px;
}

.castomNav ul li {
  text-transform: uppercase;
  color: #fff;
  margin: 40px 0;
  transform: translateX(-100%);
  transition: transform 0.4s ease-in;
}

.castomNav ul li i {
  font-size: 20px;
  margin-right: 10px;
}

.castomNav ul li + li {
  margin-left: 15px;
  transform: translateX(-150%);
}

.castomNav ul li + li + li {
  margin-left: 30px;
  transform: translateX(-200%);
}

.castomNav a {
  color: #fafafa;
  text-decoration: none;
  transition: all 0.5s;
}

.castomNav a:hover {
  color: #ff7979;
  font-weight: bold;
}

.content {
  max-width: 1000px;
}

.content h1 {
  margin: 0;
}

.content small {
  color: #555;
  font-style: italic;
}

.content p {
  color: #333;
  line-height: 1.5;
}
